import Vue from 'vue'
import App from './App.vue'
import 'view-design/dist/styles/iview.css'
// import './ui.js'
import { registerMicroApps, start } from 'qiankun'
import VueRouter from 'vue-router'

let router = new VueRouter({
  base: '/',
  mode: 'history',
  routes: []
});

Vue.use(VueRouter)

let isProduction = process.env.NODE_ENV === 'production'
console.log(process.env.NODE_ENV)

registerMicroApps([
  {
    name: 'test1',
    entry: isProduction ? '//localhost:3002' : '//localhost:8001',
    container: '#test1',
    activeRule: '/test1'
  },
  {
    name: 'test2',
    entry: isProduction ? '//localhost:3003' : '//localhost:8002',
    container: '#test2',
    activeRule: '/test2'
  }
])

start()

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
